<template>
  <div class="wrap">
    <div class="bg">
      <div>
        <div class="row">
          <p>PDF(sw)</p>
          <el-input v-model.trim="interval"
                    clearable
                    @input="interval=interval.replace(/[^\d.]/g,'')"></el-input>
        </div>
        <div class="row">
          <p>PDF</p>
          <el-input v-model.trim="scale"
                    clearable
                    @input="scale=scale.replace(/[^\d.]/g,'')"></el-input>
        </div>
      </div>
      <div v-loading="loading">
        <el-upload class="upload-demo"
                   action=""
                   :on-change="submitHandler"
                   :http-request="()=>{}"
                   accept="application/pdf"
                   ref="pdfUpload"
                   drag>
          <i class="el-icon-upload"></i>
          <span></span>
          <div class="el-upload__text"><em></em></div>
          <div class="el-upload__tip"
               slot="tip">PDF</div>
        </el-upload>
      </div>
    </div>

    <div class="bg-img pdf-list">
    </div>
  </div>
</template>
<script>

import * as PDFJS from "pdfjs-dist";
import pdfjsWorker from 'pdfjs-dist/build/pdf.worker.entry';
PDFJS.GlobalWorkerOptions.workerSrc = pdfjsWorker;

export default {
  name: "home",
  data () {
    return {
      canvasArr: [],
      loading: false,
      scale: 2,
      interval: 0,
      renderImgArr: [],
      ds: []
    };
  },
  methods: {
    submitHandler (obj) {
      this.loading = true
      console.log('--refs--', obj.raw);
      const { raw } = obj || {}
      const fObj = new FileReader()
      fObj.readAsDataURL(raw)
      fObj.onloadend = e => {
        this.transformImage(e.target.result)
      }
    },
    async transformImage (value) {
      console.log('--base64>>', value);
      const loadingTask = PDFJS.getDocument(value);
      const pdfList = document.querySelector('.pdf-list')
      loadingTask.promise.then(async (pdf) => {
        // pdf 
        let pages = pdf.numPages //pagespdf
        for (let i = 1; i <= pages; i++) { //
          let canvas = document.createElement('canvas')
          canvas.width = 200
          let page = await pdf.getPage(i) //getPage,page
          let scale = this.scale;//1
          let viewport = page.getViewport({ scale });
          let context = canvas.getContext('2d'); //canvas
          canvas.height = viewport.height; //canvas
          canvas.width = viewport.width;
          let renderContext = {
            canvasContext: context,
            viewport: viewport
          };
          await page.render(renderContext)
          // canvas.setAttribute('swale')
          canvas.className = `canvas` //canvasclass,canvas
          pdfList.appendChild(canvas) //pdfList
          this.canvasArr.push(canvas)
        }
        this.addLisenter(pdfList)
        this.loading = false
      })
    },
    dataUrlToBold (url) {
      let arr = url.split(','),
        mime = arr[0].match(/:(.*?);/)[1],
        bStr = atob(arr[1]),
        n = bStr.length,
        u8arr = new Uint8Array(n);
      while (n--) {
        u8arr[n] = bStr.charCodeAt(n);
      }
      return new Blob([u8arr], { type: mime });
    },
    addLisenter (dom) {
      dom.addEventListener('click', (e) => {

        const base64 = e.target.toDataURL('image/png')
        let blob = this.dataUrlToBold(base64);
        let obj_url = URL.createObjectURL(blob); // Chrome 
        const aDom = document.createElement('a')
        aDom.href = obj_url
        aDom.download = 'bt'
        aDom.click()
      })
    }
  }
}
</script>
